<template>
  <form>
    <h3 class="text-muted my-5">添加新的pizza</h3>
    <div class="form-group row">
      <label class="col-sm-2">品种</label>
      <div class="col-sm-10">
        <input type="text" class="form-control" v-model="newPizza.name">
      </div>
    </div>
    <div class="form-group row">
      <label class="col-sm-2">描述</label>
      <div class="col-sm-10">
        <textarea rows="5"
        class="form-control"
        style="resize: none"
        v-model="newPizza.description"
        ></textarea>
      </div>
    </div>
    <p><strong>选项1</strong></p>
    <div class="form-group row">
      <label class="col-sm-2">尺寸</label>
      <div class="col-sm-10">
        <input type="text" class="form-control" v-model="newPizza.size1">
      </div>
    </div>
    <div class="form-group row">
      <label class="col-sm-2">价格</label>
      <div class="col-sm-10">
        <input type="text" class="form-control" v-model="newPizza.price1">
      </div>
    </div>
    <p><strong>选项2</strong></p>
    <div class="form-group row">
      <label class="col-sm-2">尺寸</label>
      <div class="col-sm-10">
        <input type="text" class="form-control" v-model="newPizza.size2">
      </div>
    </div>
    <div class="form-group row">
      <label class="col-sm-2">价格</label>
      <div class="col-sm-10">
        <input type="text" class="form-control" v-model="newPizza.price2">
      </div>
    </div>
    <div class="form-group row">
      <button type="button" 
      class="btn btn-primary btn-block"
      @click="addMenuItem"
      >添加</button>
    </div>
  </form>
</template>

<script>
export default {
  data(){
    return {
      newPizza: {}
    }
  },
  methods: {
    addMenuItem(){
      // console.log(this.newPizza)
      let data = {
        name: this.newPizza.name,
        description: this.newPizza.description,
        options: [
          {size: this.newPizza.size1, price: this.newPizza.price1},
          {size: this.newPizza.size2, price: this.newPizza.price2}
        ]
      }
      //通过fetch 将数据post到数据库中 
      fetch("https://wd0199191869leiftw.wilddogio.com/menu.json", {
        method: "POST",
        headers: {
          "Content-type": 'application/json',

        },
        body: JSON.stringify(data)
      })
      // .then(res => this.$router.push({name: "menuLink"}))
      .then(res => this.$store.commit('pushToMenuItems', data))
      .catch(err => alert(err))

    }
  }
}
</script>



